    "Wirkung": "Die unbehandelte Joruga-Wurzel ist unverdaulich und verursacht Durchfall. Wird sie zu einem Fiebersaft weiterverarbeitet, kann sie bei Schwarzer Wut und Tollwut helfen.<br/>      Die Einnahme einer Dosis Joruga-l schtzt fr 4 Monate vor einer Infektion mit Schwarzer Wut. Gegen eine Tollwutinfektion wirkt dieses Mittel jedoch nicht. Wird die dreifache Menge binnen einem Tag nach einer Infektion eingenommen, senkt das die Infektionswahrscheinlichkeit auf 5 % (1 auf W20) bei Schwarzer Wut<br/>bzw. auf 20 % (1-4 auf W20) bei Tollwut. Bei Schlachtfeldfieber wird die Dauer der Krankheit um 2 Tage verkrzt, wenn ein alkoholischer Sud aus Joruga und Gulmond eingenommen wird.",
