    "Wirkung": "Fr Zwlfgttergeweihte ist der Pilz besonders gefhrlich: Bereits die bloe Berhrung kann bei ihnen zu einer Ansteckung mit den tdlichen Zorganpocken (siehe Regelwerk Seite 344) fhren. Geweihte des Namenlosen schtzen die Wirkung des Rattenpilzes besonders, erleichtert er es ihnen doch, selbst fromme Diener der Zwlfe nachhaltig zu erschttern und sie zum Glauben an den Widersacher zu bekehren. Der Pilz verstrmt eine starke Anziehungskraft auf jeden Wanderer. Der Meister wrfelt mit der Giftstufe, also 3 (13/13/13) erschwert um die SK des Helden. Bei Erfolg atmet der Charakter genug Sporen ein oder verspeist den Pilz gar, sodass Zweifel von ihm Besitzt ergreifen. Berhrt jemand mit zwlfgttlicher Geweihtentradition einen Pilz oder verzehrt ihn, wrfelt der Meister zuerst auf die Ansteckung mit Zorganpocken. Diese Krankheit ist im Regelwerk auf Seite 344 beschrieben. Die Ansteckungswahrscheinlichkeit betrgt 50 % (1-10 auf W20). Wird der Geweihte nicht infiziert, so berkommen auch ihn namenlose Zweifel.<br/><strong>Sonderregel<strong>: Der Pilz verstrmt eine starke Anziehungskraft auf jeden Wanderer. Der Meister wrfelt mit der Giftstufe, also 3 (13/13/13) erschwert um die SK des Helden. Bei Erfolg atmet der Charakter genug Sporen ein oder verspeist den Pilz gar, sodass Zweifel von ihm Besitz ergreifen. Berhrt jemand mit zwlfgttlicher Geweihtentradition einen Pilz oder verzehrt ihn, wrfelt der Meister zuerst auf die Ansteckung mit Zorganpocken. Diese Krankheit ist im Regelwerk auf Seite 344 beschrieben. Die Ansteckungswahrscheinlichkeit betrgt 50 % (1-10 auf W20). Wird der Geweihte nicht infiziert, so berkommen auch ihn namenlose Zweifel.",
