    "Wirkung": "Sansaro lindert die Folgen der Efferd- und Kerkersieche. Es kommen keine weiteren Stufen Verwirrung durch die Krankheit hinzu. Bei konstanter Einnahme ber zwei Wochen knnen beide Krankheiten sogar ganz besiegt werden. Bei Fieberkrankheiten eingenommen, kann Sansaro die verursachten SP pro Tag um 1 reduzieren. Als Sud weiterverarbeitet, verhindert es zudem einen Tag lang Ansteckungen mit allen Krankheiten  es sei denn, sie haben eine magische oder eine andere bernatrliche Ursache. Als Paste hilft die Pflanze zwei Tage lang gegen Parasiten. Nach einer Woche setzt bei der Paste aber ein Faulgeruch ein, sodass der Anwender eine Erschwernis von 1 auf alle CH-Proben erhlt, sofern Geruch eine Rolle spielt (insbesondere bei Teilproben von Fertigkeiten). Ein Sud aus Quinja, Sansaro und Donf kann fr fnf Monate einen Ausbruch des Jahresfiebers verhindern.",
