        "name": " Kegelsicht",
        "value": "(FW 12, 2 AP): Die Liturgie wirkt fr 1 KR lang innerhalb des Sichtfelds des Geweihten bis maximal 8 Schritt Entfernung auf alle in diesem Feldbefindlichen Objekte und Wesen. Das Sichtfeld des Geweihten ist nicht vernderlich, erlaubt ist also nur der Blick in eine Richtung. Da der Geweihte sich dabei nicht auf eine spezielle Sache konzentrieren kann, werden die erreichten QS vor dem Konsultieren der Tabelle halbiert. Diese Art der karmalen Wahrnehmung zhlt als ungerichtete karmale Wahrnehmung."
