      "Wirkung": "Mit dieser Liturgie lsst sich aktives magisches Wirken auf Gegenstnden und Personen erkennen. Je nach Strke der astralen Krfte im Objekt kann die Probe erleichtert oder erschwert werden. Je nach QS knnen dabei nachfolgende Analysen mittels des Zaubers ANALYS oder entsprechenden Liturgien beeinflusst werden. Der Geweihte kann nur ein ausgewhltes Wesen oder Objekt in Reichweite untersuchen. Er hat keine Rundumsicht.<br><br>QS 1: Ist Magie vorhanden oder nicht?<br>QS 2: Die maximale QS bei der Magischen Analyse steigt um 1.<br>QS 3: Die Fertigkeitsprobe eines ANALYS oder eine entsprechende Liturgie auf das gleiche Objekt ist um 1 erleichtert.<br>QS 4: Die Fertigkeitsprobe eines ANALYS oder eine entsprechende Liturgie auf das gleiche Objekt ist um 2 erleichtert.<br>QS 5: Die maximale QS bei der Magischen Analyse steigt um 2.<br>QS 6: Die Fertigkeitsprobe eines ANALYS oder eine entsprechende Liturgie auf das gleiche Objekt ist um 3 erleichtert.<br><br>Die Boni fr die maximale QS bei der Magischen Analyse sind nicht kumulativ bzw. auch nicht die Erleichterungen beim nachfolgenden ANALYS oder einer entsprechende Liturgie. Wer also QS 4 bei Magiesicht erzielt hat, bekommt keine Erleichterung von 3 (1 durch QS 3 + 2 durch QS 4), sondern nur eine von 2. Allerdings erhlt der Held die hchsten Boni der beiden Verbesserungsmglichkeiten. Ein Held mit QS 6 bekommt also eine Erleichterung von 3 auf den nachfolgenden ANALYS oder eine entsprechende Liturgie (durch QS 6) und die maximale QS bei der Magischen Analyse ist um 2 erhht (durch QS 5).",
