      "Reichweite": "selbst",
      "Steigerungsfaktor": "B",
      "Verbreitung": "Efferd (Wind), Phex (Schatten)",
      "Wirkung": "Der Krper des Phexgeweihten verwandelt sich in Nebel. Seine Kleidung und Gegenstnde, die er am Krper trug, werden nicht mitverwandelt. Profane, magische und geweihte Waffen richten gegen den Nebel keinen Schaden an. Zauber mit dem Merkmal Verwandlung knnen nicht gegen den Nebel eingesetzt werden. Der Geweihte kann sich bei relativer Windstille mit GS 4 willentlich fortbewegen; bei leichtem Gegenwind muss er eine Probe auf Willenskraft bestehen, um nicht abgetrieben zu werden; bei starkem Gegenwind wird er aber in Windrichtung davongeblasen, ohne sich dagegen wehren zu knnen. Der Geweihte kann seine Umgebung mit allen fnf Sinnen wahrnehmen. Der Geweihte bleibt so lange ein Nebel, bis die Wirkungsdauer vorber ist.",
