      "Wirkung": "Der Stab verwandelt sich in eine Peitsche, die als magische Waffe gilt. Nun hat der Magier zwei Mglichkeiten:<ul><li>#Entweder er lsst die Peitsche selbst mit AT 14 und 1W6+2 TP angreifen. Da sie wegen der nur schwer kontrollierbaren Bewegungen kaum abzuwehren ist, ist eine Parade gegen sie um 2 erschwert. Schilde knnen darber hinaus nur ihren einfachen Bonus auf die Verteidigung nutzen. Die Reichweite der Peitsche ist <em>lang</em>, man kann mit ihr keine Paraden ausfhren.</li><li>Die Peitsche kann wahlweise statt TP den Status <em>Liegend </em>verursachen. Dies muss vor der Attacke angekndigt werden. Ein Gegner kann dies verhindern, indem ihm eine Probe auf <em>Krperbeherrschung</em> <em>(Kampfmanver) </em>erschwert um 3 gelingt.</li><ul>"
