      "Reichweite": "selbst",
      "Steigerungsfaktor": "B",
      "Verbreitung": "Geoden, Hexen",
      "Wirkung": "Der Speichel der Hexe verwandelt sich augenblicklich in tzende Sure, die fr die Hexe selbst jedoch harmlos ist. Der Speichel verursacht 1W6+(QS) Trefferpunkte an dem nchsten Ziel, das die Hexe anspuckt. Die Spuckreichweite betrgt 3 Schritt. Die Hexe muss keine zustzliche Aktion aufwenden, um nach dem Wirken des Zaubers zu spucken. Das Spucken ist in der Zauberdauer inbegriffen. Die Spucke trifft automatisch, wenn der Gegner sich nicht verteidigt. Der Spuckangriff zhlt als Fernkampfangriff mit einer Schusswaffe und kann entsprechend geblockt werden, und auch ein Ausweichen ist mglich. An Schilden erzeugt er Strukturschaden, wenn er auf sie trifft. Trifft die Spucke ihr Ziel, werden die TP durch den RS des Ziels vermindert.",
