      "Steigerungsfaktor": "B",
      "Verbreitung": "Hexen",
      "Wirkung": "Die Zaubernde kann hlzerne Gegenstnde telekinetisch anheben und bewegen. Der Gegenstand kann auch andere Materialien beinhalten, die dann zum Gewicht dazu zhlen, aber zumindest ein Teil muss aus Holz bestehen. Die Gegenstnde bewegen sich mit einer Geschwindigkeit von maximal QS + 2 Schritt pro Aktion und drfen maximal QS x 20 Stein wiegen. Pro 5 Stein Gewicht muss die Zaubernde dafr 1 AsP aufbringen. Befinden sich andere Objekte an oder auf dem zu bewegenden Gegenstand, zhlt deren Gewicht mit hinzu. Die Bewegungen des Objekts sind trge, weshalb sie fr Angriffe oder Paraden ungeeignet sind. Um ein so bewegtes Objekt durch Festhalten oder Drcken aufzuhalten, ist eine um die QS erschwerte Probe auf Kraftakt (Ziehen &amp; Zerren) ntig.",
