      "Verbreitung": "Hexen",
      "Wirkung": "Die Hexe ruft mit diesem Zauberspruch bis zu QS x 5 Hornissen herbei, die fr sie kmpfen. Die Hornissen bilden maximal so viele Schwrme, wie es Gegner gibt. Die genaue Anzahl der Schwrme kann die Hexe bestimmen. Die Hexe muss einen Punkt in der Luft innerhalb der Reichweite bestimmen, an dem die Tiere dann in einem Radius von QS/2 Schritt erscheinen. Stirbt ein Gegner, verschwindet der Schwarm wieder im Nichts. Nach ihrem Erscheinen knnen die Hornissen sich beliebig weit vom Ort ihrer Herbeirufung wegbewegen. Die Hornissen sind magische Wesen, die aus Magie erschaffen wurden. Endet die Wirkungsdauer, verschwinden die Hornissenschwrme wieder im Nichts.",
