      "Verbreitung": "Gildenmagier",
      "Wirkung": "Der Verzauberte erleidet Schmerzen, die sich oft in Krmpfen manifestieren. Der Hllenpein ersetz bei Folterungen das Talent Einschchtern (Folter). Das Opfer muss eine Vergleichsprobe mit Selbstbeherrschung (Folter widerstehen) gegen den Hllenpein bestehen, die um die Schmerzstufe des Zauberspruchs erschwert ist (zustzlich<br>zu allen sonstigen Modifikatoren). Der Zaubernde hat nach dem Wirken des Zaubers QS Kampfrunden Zeit, um sein Ziel zu berhren, damit der Spruch seine Wirkung entfalten kann. Ansonsten verpufft der Zauber wirkungslos. Will ein Ziel nicht berhrt werden, ist eine gelungene, nicht verteidigte Raufen-AT notwendig.<br><br>QS 1: keine zustzliche Wirkung<br>QS 2: 1 Stufe Schmerz, 0 SP<br>QS 3: 1 Stufe Schmerz, 1 SP<br>QS 4: 2 Stufen Schmerz, 1 SP<br>QS 5: 2 Stufen Schmerz, 2 SP<br>QS 6: 3 Stufen Schmerz, 2 SP",
