      "Reichweite": "16 Schritt",
      "Steigerungsfaktor": "C",
      "Verbreitung": "Gildenmagier",
      "Wirkung": "Aus den Fingern des Zaubernden schiet einen Flammenstrahl, der in gerader Linie sein Ziel trifft. Der Magier muss keine zustzliche Aktion aufwenden,um nach dem Wirken des Zaubers zu treffen. Das Treffen ist in der Zauberdauer inbegriffen.<br><br>Das getroffene Ziel erleidet 2W6+(QS x 2) TrefferpunkteSchaden.<br><br>Der Flammenstrahl zhlt als Fernkampfangriff mit einer Schusswaffe und kann entsprechend geblockt werden, und auch ein Ausweichen ist mglich. An Schilden erzeugt er Strukturschaden, wenn er auf sie trifft. Der Strahl trifft automatisch, wenn man sich nicht verteidigt.<br><br>Trifft der Flammenstrahl sein Ziel, werden die TP durch den RS des Ziels vermindert. Entflammbare Ziele werden bei 1-3 auf 1W6 angezndet und erleidet somit den Status Brennend auf kleiner Flche.",
