      "Wirkung": "Der Zauberer trennt seinen Geist von seinem Krper und kann sich auf diesem Wege mit einer GS von QS x 3 fortbewegen, feste Objekte halten den Geisterkrper jedoch auf. Er behlt dabei Sicht, Gehr und Geruch, vermag jedoch nicht, mit seiner Umgebung zu interagieren, was auch das Wirken von Magie ausschliet. Ein Eindringen in Bereiche, in denen Magie im Allgemeinen sowie Zauber des Merkmals Sphren im Speziellen unterbunden oder zumindest erschwert werden, ist nur mglich, wenn die erreichten QS die QS der zu berwindenden Wirkung bersteigen. Whrend der Wirkungsdauer bemerkt der Zauberer nicht, was mit seinem bewusstlosen Krper geschieht, selbst wenn dieser Schaden erleidet. Stirbt der Kper, wird der Geist zu einer Gefesselten Seele. Gleiches geschieht, wenn der Geist nicht vor Ende der Wirkungsdauer in den Krper zurckkehrt, der daraufhin als leere Hlle verendet.",
