      "Wirkung": "Eine niedere Fee wird in ihre Globule zurckgeschickt, wenn der Zauberer mehr QS erzielt, als die Anrufungsschwierigkeit betrgt (Anrufungsschwierigkeiten von 0 oder hher zhlen als 0). Es kann sich dabei sowohl um ein beschworenes als auch um ein freies Feenwesen handeln. Ebenso ist es mglich, Feen zu vertreiben, die an Objekte oder Wesen gebunden bzw. in diese eingefahren sind. Bei der Vertreibung wird das Feenwesen in den Boden, einen See oder Baum gezogen und tritt in seine Globule ber. Anschlieend kann es nicht mehr eigenstndig in der Dritten Sphre in Erscheinung treten, jedoch weiterhin beschworen werden und durch ein Feentor treten.<br><br>Feenkategorien (Beispiele)<br><br>Niedere Feen: Bltenjungfern, Biestinger, Wurzelbolde<br>Mittlere Feen: Dryaden, Nymphen, groe Biestinger, Kobolde<br>Hohe Feen: Holde",
