      "Verbreitung": "Hexen",
      "Wirkung": "Um einen Punkt innerhalb der Reichweite breitet sich eine Zone mit einem Radius von QS x 2 Schritt aus. In dieser schieen dmonische Klauen, Muler oder Tentakel aus dem Boden hervor, die alles innerhalb des Wirkungsbereichs angreifen. Jedes Wesen innerhalb dieser Zone sieht sich potenziellen Angriffen der dmonischen Erscheinungen ausgesetzt und erleidet einen Abzug von 2 auf seine GS, wobei diese nicht unter 1 fallen kann. Abhngig von der Menge seiner Bewegungen muss jedes Wesen innerhalb der Pandaemonium-Zone auf die unten aufgefhrte Angriffstabelle wrfeln. Nutzt ein Wesen in einer KR keine Aktion fr eine Bewegung, muss es zu Beginn der KR einmal wrfeln. Fr jede Aktion oder freie Aktion zur Bewegung des Wesens muss sofort ein Mal auf der Tabelle gewrfelt werden.<br> <br>Angriffstabelle:<br><br>1: kein Angriff<br>2-3: Eine Kralle greift an (AT 16, 1W6+4 TP).<br>4-5: Ein Tentakel greift an (AT 14, 1W6+2 TP, wenn nicht verteidigt, dann Status Fixiert, bis zum Ende der nchsten KR; weitere Angriffe knnen erfolgen).<br>6: Ein Maul greift an (AT 12, 2W6+2 TP). Den Status Fixiert durch die Tentakel erleidet das Wesen immer bei der Hlfte der Distanz, die es zurcklegen wollte. Die Auswchse abzuschlagen ist zwecklos, da sie auf der Stelle nachwachsen.",
